* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper, body {
  overflow-x: hidden;
}

body {
  width: 100%;
  max-width: 3000px;
  margin: auto;
  font-family: Montserrat, arial, Helvetica, sans-serif;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

ul li {
  list-style: none;
}

p {
  max-width: 35em;
  margin-bottom: 1.7em;
}

h1, h2, h3 {
  font-family: Funnel Display, arial, helvetica, sans-serif;
}

.wrapper {
  width: 100%;
  height: 100vh;
}

.content {
  backdrop-filter: blur(18px);
  color: #fff;
  background-color: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 30px;
  width: 60%;
  min-height: 60vh;
  max-height: 80vh;
  padding: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 1em #0000002b;
}

nav {
  font-family: Funnel Display, arial, helvetica, sans-serif;
  font-size: 1.2em;
}

nav ul {
  justify-content: space-evenly;
  margin-top: .5em;
  display: flex;
}

nav a {
  color: #fff;
  border-radius: 10px;
  padding: 0 .1em .3em;
  position: relative;
}

nav a:after {
  content: "";
  background-color: #fff;
  width: 0%;
  height: 2px;
  transition: all .7s;
  position: absolute;
  bottom: 0;
  left: 0;
}

nav a:hover:after {
  width: 100%;
}

nav a[aria-current="page"]:after {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

h1 {
  text-align: center;
  margin-top: 2em;
}

.grid-container {
  grid-column-gap: 1em;
  grid-template-columns: 1fr 1fr;
  justify-self: center;
  justify-items: center;
  height: 100%;
  margin-top: 1em;
  padding: 1.2em;
  display: grid;
}

.grid-container .grid-box {
  margin: 1em;
  padding-right: .5em;
}

.grid-container .grid-box:first-child {
  border-right: 1px solid #fff;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #010e05;
  }

  .wrapper {
    background: url("web.ddf760d5.jpg") center / cover no-repeat;
  }
}

@media (prefers-color-scheme: light) {
  body {
    background-color: #7394b3;
  }

  .wrapper {
    background: url("feather.7736846b.jpg") center / cover no-repeat;
  }
}

@media (width <= 760px) {
  .content {
    width: 80%;
  }

  nav ul {
    flex-wrap: wrap;
  }

  nav ul li {
    padding: .3em;
  }

  .grid-container {
    flex-direction: column;
    display: flex;
  }

  .grid-container .grid-box:first-child {
    border: none;
  }
}

@media (width <= 350px) {
  .content {
    width: 95%;
    min-height: 90vh;
    padding: 0;
  }

  h1 {
    margin-top: 1em;
  }

  .grid-container {
    padding: 0;
  }
}
